













































import { computed, defineComponent, PropType } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { ContactInfoData } from '@/app/shared/client/type/contact-info';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';

const ContactInfoContentEditor = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ContactInfoData>,
    },
  },
  setup(props, { root }) {
    const i18n = useI18N(root.$store);

    const contactInfoButtonText = computed(() => {
      const contactInfoList = [];
      if (isNotNullOrEmpty(props.value?.phone)) {
        contactInfoList.push(i18n('CommonLabels.ContactInfo.Phone'));
      }

      if (isNotNullOrEmpty(props.value?.email)) {
        contactInfoList.push(i18n('CommonLabels.ContactInfo.Email'));
      }

      return contactInfoList.join(', ');
    });

    const atLeastOneContactInfo = computed(() => {
      if (isNotNullOrEmpty(props.value?.phone)) {
        return true;
      }

      if (isNotNullOrEmpty(props.value?.email)) {
        return true;
      }

      return false;
    });

    return {
      i18n,
      contactInfoButtonText,
      atLeastOneContactInfo,
    };
  },
});

export default ContactInfoContentEditor;
